.user-chat {
    position: fixed;
    left: 86px;
    bottom: 75px;
    width: 350px;
    z-index: 999;
  
    .toggle-chat {
      cursor: pointer;
      position: absolute;
      display: flex;
      align-items: center;
      border-radius: 24px;
      gap: 6px;
      color: var(--background-color);
      background-color: var(--main-color);
      bottom: -55px;
      left: -54px;
      transition: 0.8s max-width ease;
      max-width: 54px;
      overflow: hidden;

      span{
        white-space: nowrap;
        display: none;
        font-size: 12px;
      }
      &:hover{
        max-width:  200px;
        padding-inline-end: 16px ;

        span{
          display: block;
        }
      }
      svg {
        width: 48px;
        height: 48px;
      }
    }
  
    form {
      border: 1px solid #ccc;
      background-color: var(--background-color);
      box-shadow: 0px 0px 4px 0px #ccc;
      border-radius: 12px;
      overflow: hidden;
      animation: drop_up 0.4s ;

    }
    h2 {
      padding: 6px 12px;
      border-bottom: 1px solid #ccc;
      font-size: 16px;
      font-weight: 600;
      display: flex;
      align-items: center;
      justify-content: space-between;
      span{
        &:last-child{
          cursor: pointer;
          position: relative;
          transform: rotate(90deg);
        }

      }
    }

    ul {
      height: 400px;
      list-style: none;
      overflow-y: auto;
      display: flex;
      flex-direction: column;
      padding: 12px;
      font-size: 14px;
  
      &::-webkit-scrollbar {
        width: 8px;
      }
  
      &::-webkit-scrollbar-thumb {
        background: #d6d6d6;
        /* لون الشريط */
        border-radius: 6px;
      }
  
      li {
        margin-block: 12px;
  
        span {
          display: inline-block;
          border-radius: 20px;
          padding: 12px;
          max-width: 100%;
        }
      }
  
      .left {
        margin-inline-start: auto;
  
        span {
          max-width: 100%;
          line-break: loose;
          display: inline-block;
          background-color: #ccc;
        }
      }
  
      .right {
        span {
          background-color: var(--main-color);
          color: var(--background-color);
        }
      }
    }
  
    .input-group-chat {
      display: flex;
      border-top: 1px solid #ccc;
      background-color: #F5F5F5;
  
      button {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 12px;
        background: none;
        border: none;
        height: 100%;
  
        svg {
          width: 40px;
          height: 40px;
        }
      }
  
      input {
        width: 100%;
        border: none;
        outline: none;
        padding: 6px 12px;
      background-color: #F5F5F5;

      }
    }
  }
  
  .admin-chat {
    display: grid;
    grid-template-columns: 200px calc(100% - 200px);
  
    .chat-users {
      height: 100vh;
      border: 1px solid #ccc;
  
      ul {
        position: relative;
        height: 100%;
        list-style: none;
        overflow-y: auto;
  
        &::-webkit-scrollbar {
          width: 8px;
        }
  
        &::-webkit-scrollbar-thumb {
          background: #d6d6d6;
          /* لون الشريط */
          border-radius: 6px;
        }
  
        .active {
          background-color: var(--main-color);
          color: var(--background-color);
  
          svg {
            path {
              fill: var(--background-color) !important;
            }
          }
        }
  
        li {
          display: flex;
          align-items: center;
          justify-content: space-between;
          font-weight: 500;
          padding: 12px;
          cursor: pointer;
          border: 1px solid #ccc;
          text-transform: uppercase;
        }
  
        span {
          position: absolute;
          display: block;
          top: 30%;
          transform: translateY(-50%);
          width: 100%;
          text-align: center;
        }
  
        .icon-user {
          display: flex;
          align-items: center;
          gap: 12px;
        }
      }
    }
  
    .user-chat {
      width: 100%;
      height: 100vh;
      position: static;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      border-radius: 0;
  
      h2 {
        padding: 24px;
        text-transform: uppercase;
      }
  
      ul {
        height: 87vh;
        padding-inline: 2%;
      }
  
      .input-group-chat {
        margin-top: auto;
      }
    }
  }
  @keyframes drop_up {
    0%{
      opacity: 0;
      transform: translateY(50px);
    }100%{
      opacity: 1;
      transform: translateY(0);

    }
  }