@charset "UTF-8";
.user-chat {
  position: fixed;
  left: 86px;
  bottom: 75px;
  width: 350px;
  z-index: 999;
}
.user-chat .toggle-chat {
  cursor: pointer;
  position: absolute;
  display: flex;
  align-items: center;
  border-radius: 24px;
  gap: 6px;
  color: var(--background-color);
  background-color: var(--main-color);
  bottom: -55px;
  left: -54px;
  transition: 0.8s max-width ease;
  max-width: 54px;
  overflow: hidden;
}
.user-chat .toggle-chat span {
  white-space: nowrap;
  display: none;
  font-size: 12px;
}
.user-chat .toggle-chat:hover {
  max-width: 200px;
  padding-inline-end: 16px;
}
.user-chat .toggle-chat:hover span {
  display: block;
}
.user-chat .toggle-chat svg {
  width: 48px;
  height: 48px;
}
.user-chat form {
  border: 1px solid #ccc;
  background-color: var(--background-color);
  box-shadow: 0px 0px 4px 0px #ccc;
  border-radius: 12px;
  overflow: hidden;
  animation: drop_up 0.4s;
}
.user-chat h2 {
  padding: 6px 12px;
  border-bottom: 1px solid #ccc;
  font-size: 16px;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.user-chat h2 span:last-child {
  cursor: pointer;
  position: relative;
  transform: rotate(90deg);
}
.user-chat ul {
  height: 400px;
  list-style: none;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  padding: 12px;
  font-size: 14px;
}
.user-chat ul::-webkit-scrollbar {
  width: 8px;
}
.user-chat ul::-webkit-scrollbar-thumb {
  background: #d6d6d6;
  /* لون الشريط */
  border-radius: 6px;
}
.user-chat ul li {
  margin-block: 12px;
}
.user-chat ul li span {
  display: inline-block;
  border-radius: 20px;
  padding: 12px;
  max-width: 100%;
}
.user-chat ul .left {
  margin-inline-start: auto;
}
.user-chat ul .left span {
  max-width: 100%;
  line-break: loose;
  display: inline-block;
  background-color: #ccc;
}
.user-chat ul .right span {
  background-color: var(--main-color);
  color: var(--background-color);
}
.user-chat .input-group-chat {
  display: flex;
  border-top: 1px solid #ccc;
  background-color: #F5F5F5;
}
.user-chat .input-group-chat button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px;
  background: none;
  border: none;
  height: 100%;
}
.user-chat .input-group-chat button svg {
  width: 40px;
  height: 40px;
}
.user-chat .input-group-chat input {
  width: 100%;
  border: none;
  outline: none;
  padding: 6px 12px;
  background-color: #F5F5F5;
}

.admin-chat {
  display: grid;
  grid-template-columns: 200px calc(100% - 200px);
}
.admin-chat .chat-users {
  height: 100vh;
  border: 1px solid #ccc;
}
.admin-chat .chat-users ul {
  position: relative;
  height: 100%;
  list-style: none;
  overflow-y: auto;
}
.admin-chat .chat-users ul::-webkit-scrollbar {
  width: 8px;
}
.admin-chat .chat-users ul::-webkit-scrollbar-thumb {
  background: #d6d6d6;
  /* لون الشريط */
  border-radius: 6px;
}
.admin-chat .chat-users ul .active {
  background-color: var(--main-color);
  color: var(--background-color);
}
.admin-chat .chat-users ul .active svg path {
  fill: var(--background-color) !important;
}
.admin-chat .chat-users ul li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 500;
  padding: 12px;
  cursor: pointer;
  border: 1px solid #ccc;
  text-transform: uppercase;
}
.admin-chat .chat-users ul span {
  position: absolute;
  display: block;
  top: 30%;
  transform: translateY(-50%);
  width: 100%;
  text-align: center;
}
.admin-chat .chat-users ul .icon-user {
  display: flex;
  align-items: center;
  gap: 12px;
}
.admin-chat .user-chat {
  width: 100%;
  height: 100vh;
  position: static;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 0;
}
.admin-chat .user-chat h2 {
  padding: 24px;
  text-transform: uppercase;
}
.admin-chat .user-chat ul {
  height: 87vh;
  padding-inline: 2%;
}
.admin-chat .user-chat .input-group-chat {
  margin-top: auto;
}

@keyframes drop_up {
  0% {
    opacity: 0;
    transform: translateY(50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}/*# sourceMappingURL=Chat.css.map */